// export const baseUrl = 'http://139.198.2.158:8081/api'
export const baseUrl = 'http://114.255.78.182:8080/api'
// export const baseUrl = 'api'

export const config = {
  headers: {
    'Content-Type': 'application/json;charset=utf-8'
  }
}

export function formatData(list, path) {
  //user
  if (path == '/user') {
    list.forEach(function(item) {
      if (item['type'] == 1) {
        item['type'] = ''
      }else if (item['type'] == 0) {
        item['type'] = ''
      }
    })
  }
  //
  if(path == '/parking') {
    list.forEach(function(item) {
      //
      if (item['type'] == 1) {
        item['type'] = ''
      }else if (item['type'] == 2) {
        item['type'] = ''
      }else if (item['type'] == 3) {
        item['type'] = ''
      }else if (item['type'] == 4) {
        item['type'] = ''
      }
      //
      if (item['accessMode'] == 1) {
        item['accessMode'] = ''
      }else if (item['accessMode'] == 2) {
        item['accessMode'] = ''
      }
    })
  }
  //sim
  if(path == '/simCard') {
    list.forEach(function(item) {
      //
      if (item['operatorType'] == 1) {
        item['operatorType'] = ''
      }else if(item['operatorType'] == 2) {
        item['operatorType'] = ''
      }else if(item['operatorType'] == 3) {
        item['operatorType'] = ''
      }
      //sim-
      if (item['networkMode'] == 1) {
        item['networkMode'] = '3G'
      }else if(item['networkMode'] == 2) {
        item['networkMode'] ='4G'
      }
      //sim-
      if(item['deviceStatus'] == 1) {
        item['deviceStatus'] = ''
      }else if(item['deviceStatus'] == 2) {
        item['deviceStatus'] = ''
      }
    })
  }
  //
  if (path == '/controlCard') {
    list.forEach(function(item) {
      //
      if (item['deviceStatus'] == 1) {
        item['deviceStatus'] = ''
      }else if(item['deviceStatus'] == 0) {
        item['deviceStatus'] = ''
      }
      //
      if (item['initStatus'] == 1) {
        item['initStatus'] = ''
      }else if(item['initStatus'] == 0) {
        item['initStatus'] = ''
      }
    })
  }
  //
  if (path == '/screen') {
    list.forEach(function(item) {
      //
      if (item['type'] == 1) {
        item['type'] = ''
      }else if(item['type'] == 2) {
        item['type'] = ''
      }
    })
  }
  //
  if (path == '/program') {
    list.forEach(function(item) {
      //
      if (item['status'] == -1) {
        item['status'] = ''
      }else if (item['status'] == -2) {
        item['status'] = ''
      }else if (item['status'] == 0) {
        item['status'] = ''
      }else if (item['status'] == 1) {
        item['status'] = ''
      }else if (item['status'] == 2) {
        item['status'] = ''
      }
    })
  }
  //
  if (path == '/geoSensor') {
    list.forEach(function(item) {
      //
      if (item['type'] == 1) {
        item['type'] = ''
      }else if(item['type'] == 2) {
        item['type'] = ''
      }
      //
      if (item['deviceStatus'] == 1) {
        item['deviceStatus'] = ''
      }else if(item['deviceStatus'] == 2) {
        item['deviceStatus'] = ''
      }
    })
  }
  //&&
  if (path == '/repeater' || path == '/gateway') {
    list.forEach(function(item) {
      //
      if (item['deviceStatus'] == 1) {
        item['deviceStatus'] = ''
      }else if(item['deviceStatus'] == 2) {
        item['deviceStatus'] = ''
      }
    })
  }
  //
  if (path == '/inducePlate') {
    list.forEach(function(item) {
      //
      if (item['level'] == 1) {
        item['level'] = ''
      }else if (item['level'] == 2) {
        item['level'] = ''
      }else if (item['level'] == 3) {
        item['level'] = ''
      }else if (item['level'] == 4) {
        item['level'] = 'LED'
      }
    })
  }
}



