<template>
  <div>
    <!-------------------------------------------------------------------------------->
    <div class="index-filter-container">
      <el-form :model="searchInfo" :inline="true" ref="searchInfo" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
        <el-form-item prop="username">
          <el-input type="text" v-model="searchInfo.username"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="realName">
          <el-input type="text" v-model="searchInfo.realName"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="phone">
          <el-input type="text" v-model="searchInfo.phone"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" type="primary"  icon="search"
                     @click="_search(searchInfo)"></el-button>
        </el-form-item>
        <!--<el-form-item>-->
          <!--<el-button class="filter-item" style="margin-left: 10px;" @click="_clickFlag('add', '')" type="primary" icon="edit"></el-button>-->
        <!--</el-form-item>-->
      </el-form>
    </div>
    <!-------------------------------------------------------------------------------->
    <div class="index-main-table">
      <el-table :data="list"  border v-loading="listLoading" element-loading-text="" fit highlight-current-row  style="width: 100%" >
        <el-table-column prop="username" label="" min-width="8%" align="center"></el-table-column>
        <el-table-column prop="realName" label="" min-width="12%" align="center"></el-table-column>
        <el-table-column prop="phone" label="" min-width="12%" align="center"></el-table-column>
        <el-table-column prop="lastLoginIp" label="IP" min-width="13%" align="center"></el-table-column>
        <el-table-column prop="lastLoginTime" label="" min-width="15%" align="center"></el-table-column>
        <el-table-column prop="type" label="" min-width="11%" align="center"></el-table-column>
        <!-------------------------------------------------------------->
        <el-table-column align="left" label="" min-width="22%" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" v-if="scope.row.id != 1" type="info" @click="_clickFlag('edit', '', '', scope.row)"></el-button>
            <el-button size="small" v-if="scope.row.id != 1" type="success"  @click="_resetPassword(scope.row)"></el-button>
            <el-button size="small" v-if="scope.row.id != 1" type="danger" @click="_delId(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--------------------------------------------------------------------->
    <div class="index-main-pagination" v-show="!listLoading">
      <div class="block" align="right">
        <div class="block">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="num"
            layout="total, prev, pager, next"
            :total="count.totalElements">
          </el-pagination>
        </div>
      </div>
    </div>
    <!------------------------------------------------------------------------------>
    <el-dialog title="" align="left" :modal="false" :visible.sync="dialogDel" size="tiny">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogDel = false"> </el-button>
        <el-button type="primary" @click="_del(delRow, 'delete')"> </el-button>
      </span>
    </el-dialog>
    <!------------------------------------------------------------------------------>
    <el-dialog title="" size="tiny" :visible.sync="dialogResetShow" align="left">
      <el-form :model="dialogResetInfo" ref="dialogResetInfo" :rules="rules">
        <el-form-item label="" :label-width="formLabelWidth" prop="password">
          <el-input  auto-complete="off" v-model="dialogResetInfo.password" style="width: 200px" placeholder="123456"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogResetShow = false"> </el-button>
        <el-button type="primary" @click="_reset(dialogResetInfo)"> </el-button>
      </div>
    </el-dialog>
    <!-----------------------------------/------------------------------------------->
    <el-dialog :title="dialogTitle" size="tiny" :visible.sync="dialogShow" align="left" @close="dialogInfo={}">
      <el-form ref="dialogInfo" :model="dialogInfo" :rules="rules">
        <el-form-item  label="" :label-width="formLabelWidth" prop="username" >
          <el-input  auto-complete="off" v-model="dialogInfo.username" style="width: 200px" placeholder=""></el-input>
        </el-form-item>
        <el-form-item v-if="dialogType =='add'" label="" :label-width="formLabelWidth" prop="password">
          <el-input  auto-complete="off" v-model="dialogInfo.password" style="width: 200px" :placeholder="dialogType=='reset'?'123456':''"></el-input>
        </el-form-item>
        <el-form-item  label="" :label-width="formLabelWidth" prop="realName">
          <el-input  auto-complete="off" v-model="dialogInfo.realName" style="width: 200px" placeholder=""></el-input>
        </el-form-item>
        <el-form-item  label="" :label-width="formLabelWidth" prop="phone">
          <el-input  auto-complete="off" v-model="dialogInfo.phone" style="width: 200px"></el-input>
        </el-form-item>
        <!--<el-form-item v-if="dialogType =='add'" label="" :label-width="formLabelWidth" prop="salt">-->
          <!--<el-input  auto-complete="off" v-model="dialogInfo.salt" style="width: 200px"></el-input>-->
        <!--</el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false; dialogInfo = {};"> </el-button>
        <el-button type="primary" @click="_add_submit(dialogInfo, path)"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
  import apiMethods from 'api/apiMethods.js'
  import {del, reset} from "api/getData";
  import {baseUrl, config} from 'api/config'

  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value.length < 6 ) {
          callback(new Error('6'));
        } else {
          callback();
        }
      };
      var validatePhone = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value.length != 11 ) {
          callback(new Error('11'));
        } else {
          callback();
        }
      };
      var validateSalt = (rule, value, callback) => {
        if (value.length > 8) {
          callback(new Error('8'))
        } else {
          callback();
        }
      };
      return {
        path: '/user',                   //
        listLoading: true,               //
        list: [],                        //
        page: 1,                         //
        num: 10,                         //
        searchInfo: {},                  //
        pagination: false,               //
        count: {                         //
          numberOfElements: 0,
          totalElements: 0,
        },
        userStatus: false,               //
        dialogShow: false,               //
        dialogResetShow: false,          //
        dialogInfo: {},                  //
        dialogResetInfo: {},             //
        dialogTitle: "",                 //
        dialogType: "",                  //
        dialogDel: false,
        dleRow: [],
        formLabelWidth: '100px',
        rules: {                          //
            username: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ],
            password: [
              {required: true,  validator: validatePass, trigger: 'change'},
            ],
            realName: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ],
            phone: [
              {required: true,  validator: validatePhone, trigger: 'change'}
            ],
            salt: [
              {required: false, validator: validateSalt, trigger: 'change'}
            ]
        }
      }
    },
    watch:{
      searchInfo() {
      }
    },
    created() {
      this._list(this.page, this.path)
    },
    methods: {
      _resetPassword(row) {
        this.dialogType = 'reset'
        this.dialogResetShow = true;
//        console.log(row)
        this.dialogResetInfo.id = row.id;
        this.dialogResetInfo.password = "123456"
      },
      _reset(dialogResetInfo) {
        this.$refs.dialogResetInfo.validate((valid) => {
          if (valid) {
            let formData = JSON.stringify(this.dialogResetInfo);
            reset(baseUrl + this.path + '/reset', formData, config).then((res) => {
              if (res.data.code == 200) {
                this.$message({showClose: true, message: res.data.message, type: "success"})
                this._list(this.page);
                this.dialogResetShow = false;
                this.dialogResetInfo = {};
              }else if(res.data.code == -200  || res.data.code == false) {
                this.$message({showClose: true, message: res.data.message, type: "error"})
              }
            })
          }else {
            return false
          }
        })
      }
    },
    mixins: [apiMethods]
  }
</script>




