<template>
  <div>
    <!-------------------------------------------------------------------------------->
    <div class="index-filter-container">
      <el-form :model="searchInfo" :inline="true" ref="searchInfo" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
        <el-form-item prop="code">
          <el-input type="text" v-model="searchInfo.code"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="name">
          <el-input type="text" v-model="searchInfo.name"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" type="primary"  icon="search"
                     @click="_search(searchInfo)"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" @click="_clickFlag('add', '')" type="primary" icon="edit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-------------------------------------------------------------->
    <div class="index-main-table">
      <el-table :data="list"  border v-loading="listLoading" element-loading-text="" fit highlight-current-row  style="width: 100%" >
        <el-table-column prop="code" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="name" label="" min-width="20%" align="center"></el-table-column>
        <el-table-column prop="type" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="accessMode" label="" min-width="15%" align="center"></el-table-column>
        <el-table-column prop="lotTotalCount" label="" min-width="15%" align="center"></el-table-column>
        <el-table-column prop="lotRemainCount" label="" min-width="15%" align="center"></el-table-column>
        <!-------------------------------------------------------------->
        <el-table-column align="left" label="" min-width="15%" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" type="info" @click="_clickFlag('edit', '', '', scope.row)"></el-button>
            <el-button size="small" type="danger" @click="_bind(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--------------------------------------------------------------------->
    <div class="index-main-pagination" v-show="!listLoading">
      <div class="block" align="right">
        <div class="block">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="num"
            layout="total, prev, pager, next"
            :total="count.totalElements">
          </el-pagination>
        </div>
      </div>
    </div>
    <!-----------------------------------/---------------------->
    <el-dialog :title="dialogTitle" size="tiny" :visible.sync="dialogShow" align="left" @close="dialogInfo={}">
      <el-form :model="dialogInfo" ref="dialogInfo" :rules="rules">
        <el-form-item label="" :label-width="formLabelWidth" prop="code" >
          <el-input  auto-complete="off" v-model="dialogInfo.code" style="width: 90%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="name" >
          <el-input  auto-complete="off" v-model="dialogInfo.name" style="width: 90%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="unitName" >
          <el-input  auto-complete="off" v-model="dialogInfo.unitName" style="width: 90%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="mediaScreenIds">
          <el-select v-model="dialogInfo.mediaScreenId"  placeholder="" style="width: 90%">
            <el-option
              v-for="item in mediaScreenList"
              :key="item.code"
              :label="item.code"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="type">
          <el-radio-group v-model="dialogInfo.type" style="width: 110%">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="accessMode">
          <el-radio-group v-model="dialogInfo.accessMode" style="width: 100%">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="lotTotalCount" >
          <el-input  auto-complete="off" v-model="dialogInfo.lotTotalCount" style="width: 90%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="lotRemainCount" >
          <el-input  auto-complete="off" v-model="dialogInfo.lotRemainCount" style="width: 90%" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false; dialogInfo={}"> </el-button>
        <el-button type="primary" @click="_add_submit(dialogInfo)"> </el-button>
      </div>
    </el-dialog>
    <!------------------------------------------------------------------------------>
    <Modal v-model="dialogBind" title="">
      <Form ref="dialogBindInfo" :model="dialogBindInfo" align="left">
        <FormItem prop="name" label="">
          <Input  type="text"  v-model="dialogBindInfo.name" style="width: 80%" />
        </FormItem>
        <FormItem  label="">
          <Select v-model="dialogBindInfo.screenIds" multiple placeholder="" style="width:80%">
            <Option v-for="item in screenList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogBind = false;"> </el-button>
        <el-button type="primary" @click="_bindSubmit(dialogBindInfo)"> </el-button>
      </div>
    </Modal>
  </div>
</template>

<script type="text/javascript">
  import apiMethods from 'api/apiMethods.js'
  import {baseUrl, config} from 'api/config'
  export default {
    data() {
      var validateTotalCount = (rule, value, callback) => {
        if (value == '') {
          callback(new Error(''));
        } else if (value <= 0) {
          callback(new Error('0'));
        } else {
          callback();
        }
      };
      var validateRemainCount = (rule, value, callback) => {
        if (value == '') {
          callback(new Error(''));
        } else if (value < 0) {
          callback(new Error('0'));
        } else if (value > parseInt(this.dialogInfo.lotTotalCount) || value == 0) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        path: '/parking',                   //
        listLoading: true,               //
        list: [],                        //
        delRow: [],
        page: 1,                         //
        num: 10,                         //
        searchInfo: {},                  //
        pagination: false,               //
        count: {                         //
          numberOfElements: 0,
          totalElements: 0,
        },
        dialogShow: false,               //
        dialogInfo: {
          lotTotalCount: '',
          lotRemainCount: ''
        },                  //
        dialogTitle: "",                 //
        dialogType: "",                  //
        dialogDel: false,
        dialogBind: false,               //
        dialogBindInfo: {
          screenIds: []
        },
        screenList: [],                  //
        screenIds: [],
        screen: [],
        mediaScreenList: [],
        mediaScreenId: [],
        formLabelWidth: '100px',
        rules: {                           //
            code: [
              {required: true,  message: "", trigger: 'blur'}
            ],
            name: [
              {required: true,  message: "", trigger: 'blur'}
            ],
            type: [
              {required: true, type:'number', message: "", trigger: 'blur'}
            ],
            accessMode: [
              {required: true, type:'number',  message: "", trigger: 'blur'}
            ],
            lotTotalCount: [
              {required: true, validator: validateTotalCount, trigger: 'blur'}
            ],
            lotRemainCount: [
              {required: true, validator: validateRemainCount, trigger: 'blur'}
            ]
        }
      }
    },
    created() {
      this._list(this.page)
      axios.post(baseUrl + '/screen/list/2').then((res) => {
        if (res.data.code == 200) {
          this.mediaScreenList = res.data.data;
        }
      })
    },
    methods: {
      //
      _bind(row) {
        this.dialogBind = true;
        this.dialogBindInfo = row;
        //
        axios.post(baseUrl + '/screen/list/1').then((res) => {
          if (res.data.code == 200) {
            this.screenList = res.data.data;
          }
        }).catch((err) => {
          console.log(err)
        })
        //
        axios.post(baseUrl + '/relationScreen/parking/list/' + row.id).then((res) => {
          this.screen = res.data.data
          for (var i=0; i<this.screen.length; i++) {
            this.screenIds.push(this.screen[i].id)
          }
          this.dialogBindInfo.screenIds = this.screenIds
          this.screenIds = [];
        }).catch((err) => {
          console.log(err)
        })
      },
      _bindSubmit(info) {
        axios.post(baseUrl + this.path + '/bind' + '/' + info.id, info.screenIds, config).then((res) => {
          if (res.data.code == 200) {
            this.$message({showClose: true, message: res.data.message, type: "success"})
            this.dialogBind = false;
            this.dialogBindInfo.screenIds = [];
            this._list(this.page);
          }else if (res.data.success == false) {
            this.$message({showClose: true, message: res.data.message, type: "error"})
            this.dialogBindInfo.screenIds = [];
            this._list(this.page);
          }else if(res.data.code == -200) {
            this.$message({showClose: true, message: res.data.message, type: "error"})
            this.dialogBindInfo.screenIds = [];
            this._list(this.page);
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    mixins: [apiMethods]
  }
</script>




