<template>
  <div>
    <!-------------------------------------------------------------------------------->
    <div class="index-filter-container">
      <el-form :model="searchInfo" :inline="true" ref="searchInfo" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
        <el-form-item prop="name" style="width: 10%">
          <el-input type="text" v-model="searchInfo.name"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="code" style="width: 10%">
          <el-input type="text" v-model="searchInfo.code"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item style="width: 15%">
          <el-select v-model="searchInfo.controlCardId"  placeholder="" clearable>
            <el-option
              v-for="item in controlCardIdList"
              :key="item.code"
              :label="item.code"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="width: 15%">
          <el-select v-model="searchInfo.inducePlateId"  placeholder="" clearable>
            <el-option
              v-for="item in inducePlateIdList"
              :key="item.code"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="width: 15%">
          <el-select v-model="searchInfo.type"  placeholder="" clearable>
            <el-option
              v-for="item in screenTypeList"
              :key="item.type"
              :label="item.typeText"
              :value="item.type">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" type="primary"  icon="search"
                     @click="_search(searchInfo)"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" @click="_clickFlag('add', '')" type="primary" icon="edit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-------------------------------------------------------------->
    <div class="index-main-table">
      <el-table :data="list"  border v-loading="listLoading" element-loading-text="" fit highlight-current-row  style="width: 100%" >
        <el-table-column prop="name" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="code" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="type" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="controlCardCode" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="inducePlateName" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="orderNum" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="createTime" label="" min-width="15%" align="center"></el-table-column>
        <!-------------------------------------------------------------->
        <el-table-column align="left" label="" min-width="10%" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" type="info" @click="_clickFlag2('edit', '', '', scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--------------------------------------------------------------------->
    <div class="index-main-pagination" v-show="!listLoading">
      <div class="block" align="right">
        <div class="block">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="num"
            layout="total, prev, pager, next"
            :total="count.totalElements">
          </el-pagination>
        </div>
      </div>
    </div>
    <!-----------------------------------/---------------------->
    <el-dialog :title="dialogTitle" size="tiny" :visible.sync="dialogShow" align="left" @close="dialogInfo={}">
      <el-form ref="dialogInfo" :model="dialogInfo" :rules="rules">
        <el-form-item label="" :label-width="formLabelWidth" prop="name" >
          <el-input  auto-complete="off" v-model="dialogInfo.name" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="code" >
          <el-input  auto-complete="off" v-model="dialogInfo.code" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="type">
          <el-radio-group v-model="dialogInfo.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="size">
          <el-radio-group v-model="dialogInfo.size">
            <el-radio label="16*8"></el-radio>
            <el-radio label="160*32"></el-radio>
            <el-radio label="80*64"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="controlCardId">
          <el-select v-model="dialogInfo.controlCardId"  placeholder="" style="width: 80%">
            <el-option
              v-for="item in controlCardIdList"
              :key="item.code"
              :label="item.code"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="inducePlateId">
          <el-select v-model="dialogInfo.inducePlateId"  placeholder="" style="width: 80%">
            <el-option
              v-for="item in inducePlateIdList"
              :key="item.code"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="orderNum" >
          <el-input  auto-complete="off" v-model="dialogInfo.orderNum" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false; dialogInfo={}"> </el-button>
        <el-button type="primary" @click="_add_submit2(dialogInfo)"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
  import apiMethods from 'api/apiMethods.js'
  import {baseUrl} from 'api/config'
  import {add, edit} from 'api/getData'

  export default {
    data() {
      var validateWidth = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      var validateHeight = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        path: '/screen',                  //
        listLoading: true,               //
        list: [],                        //
        delRow: [],
        page: 1,                         //
        num: 10,                         //
        searchInfo: {},                  //
        pagination: false,               //
        count: {                         //
          numberOfElements: 0,
          totalElements: 0,
        },
        dialogShow: false,               //
        dialogInfo: {},                  //
        dialogTitle: "",                 //
        dialogType: "",                  //
        dialogDel: false,
        formLabelWidth: '100px',
        controlCardIdList: [],
        inducePlateIdList: [],
        screenTypeList: [
          {
            type: 1,
            typeText: ''
          },
          {
            type: 2,
            typeText: ''
          }
        ],
        rules: {                           //
            name: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ],
            code: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ],
            type: [
              {required: true, type: 'number', message: " ", trigger: 'blur'}
            ],
            size: [
              {required: true, type: 'string', message: " ", trigger: 'blur'}
            ],
            width: [
              {required: true, validator: validateWidth, trigger: 'blur'}
            ],
            height: [
              {required: true, validator: validateHeight, trigger: 'blur'}
            ],
            controlCardId: [
              {required: true,  type: 'number', message: " ", trigger: 'blur'}
            ],
            inducePlateId: [
              {required: true,  type: 'number', message: " ", trigger: 'blur'}
            ],
            orderNum: [
              {required: true, type: "string", message: "", trigger: 'blur'}
            ]
        }
      }
    },
    created() {
      this._list(this.page)
      this._getControlCardIdList()
      this._getInducePlateIdList()
    },
    methods: {
      _getControlCardIdList() {
        axios.post(baseUrl + '/controlCard/list').then((res) => {
          if (res.data.code == 200) {
            this.controlCardIdList = res.data.data
          }
        }).catch((err) => {
          console.log(err)
        })
      },

      _getInducePlateIdList() {
        axios.post(baseUrl + '/inducePlate/list').then((res) => {
          if (res.data.code == 200) {
            this.inducePlateIdList = res.data.data
          }
        }).catch((err) => {
          console.log(err)
        })
      },
      //
      _clickFlag2(type, addTitle, editTitle, row) {
        this.dialogShow = true;
        this.dialogType = type;
        if (type == 'add') {
          this.dialogTitle = addTitle;
          row = ''
        }else if (type == 'edit') {
          this.dialogTitle = editTitle;
          //
          this._edit2(row)
        }
      },
      //
      _edit2(row) {
        edit(baseUrl + this.path + "/" + row.id).then((res) => {
          if(res.data.code == 200) {
            this.dialogInfo = res.data.data
            this.dialogInfo.size = this.dialogInfo.width + '*' + this.dialogInfo.height;
          }
        }).catch((err) => {
          console.log(err)
        })
      },
      //
      _add_submit2(info) {
        this.dialogInfo.width = this.dialogInfo.size.split('*')[0]
        this.dialogInfo.height = this.dialogInfo.size.split('*')[1]
        if (this.dialogType == 'add') {
          this._add(info)
        }else if (this.dialogType == 'edit') {
          this._submit(info)
        }
      }
    },
    mixins: [apiMethods]
  }
</script>




