<template>
  <div>
    <!-------------------------------------------------------------------------------->
    <div class="index-filter-container">
      <el-form :model="searchInfo" :inline="true" ref="searchInfo" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
        <el-form-item prop="name">
          <el-input type="text" v-model="searchInfo.name"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="value">
          <el-input type="text" v-model="searchInfo.value"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" type="primary"  icon="search"
                     @click="_search(searchInfo)"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" @click="_clickFlag('add', '')" type="primary" icon="edit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-------------------------------------------------------------->
    <div class="index-main-table">
      <el-table :data="list"  border v-loading="listLoading" element-loading-text="" fit highlight-current-row  style="width: 100%" >
        <el-table-column prop="name" label="" min-width="20%" align="center"></el-table-column>
        <el-table-column prop="value" label="" min-width="25%" align="center"></el-table-column>
        <el-table-column prop="description" label="" min-width="40%" align="left" :show-overflow-tooltip="true" header-align="center"></el-table-column>
        <!-------------------------------------------------------------->
        <el-table-column align="left" label="" min-width="15%" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" type="info" @click="_clickFlag('edit', '', '', scope.row)"></el-button>
            <el-button size="small" type="danger" @click="_delId(scope.row, 'delete')"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--------------------------------------------------------------------->
    <div class="index-main-pagination" v-show="!listLoading">
      <div class="block" align="right">
        <div class="block">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="num"
            layout="total, prev, pager, next"
            :total="count.totalElements">
          </el-pagination>
        </div>
      </div>
    </div>
    <!------------------------------------------------------------------------------>
    <el-dialog title="" align="left" :modal="false" :visible.sync="dialogDel" size="tiny">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogDel = false"> </el-button>
        <el-button type="primary" @click="_del(delRow, 'delete')"> </el-button>
      </span>
    </el-dialog>
    <!-----------------------------------/---------------------->
    <el-dialog :title="dialogTitle" size="tiny" :visible.sync="dialogShow" align="left" @close="dialogInfo={}">
      <el-form ref="dialogInfo" :model="dialogInfo" :rules="rules">
        <el-form-item label="" :label-width="formLabelWidth" prop="name" >
          <el-input  auto-complete="off" v-model="dialogInfo.name" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="value" >
          <el-input  auto-complete="off" v-model="dialogInfo.value" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="description" >
          <el-input auto-complete="off" v-model="dialogInfo.description" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false; dialogInfo={}"> </el-button>
        <el-button type="primary" @click="_add_submit(dialogInfo)"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
  import apiMethods from 'api/apiMethods.js'
  import {baseUrl} from 'api/config'
  export default {
    data() {
      return {
        path: '/config',                   //
        listLoading: true,               //
        list: [],                        //
        delRow: [],
        page: 1,                         //
        num: 10,                         //
        searchInfo: {},                  //
        pagination: false,               //
        count: {                         //
          numberOfElements: 0,
          totalElements: 0,
        },
        dialogShow: false,               //
        dialogInfo: {},                  //
        dialogTitle: "",                 //
        dialogType: "",                  //
        dialogDel: false,
        formLabelWidth: '100px',
        dictPath1: '',            //
        dictValue1: '',
        dictPath2: '',
        dictValue2: '',
        rules: {                           //
            name: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ],
            value: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ],
            description: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ]
        }
      }
    },
    created() {
      this._list(this.page)
    },
    methods: {
    },
    mixins: [apiMethods]
  }
</script>




