<template>
  <div>
    <!-------------------------------------------------------------------------------->
    <div class="index-filter-container">
      <el-form :model="searchInfo" :inline="true" ref="searchInfo" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
        <el-form-item prop="username">
          <el-input type="name" v-model="searchInfo.name"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="username">
          <el-input type="code" v-model="searchInfo.code"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="username">
          <el-select v-model="searchInfo.level"  placeholder="" clearable>
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" type="primary"  icon="search"
                     @click="_search(searchInfo)"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" @click="_clickFlag('add', '')" type="primary" icon="edit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-------------------------------------------------------------->
    <div class="index-main-table">
      <el-table :data="list"  border v-loading="listLoading" element-loading-text="" fit highlight-current-row style="width: 100%" >
        <el-table-column prop="code" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="level" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="name" label="" min-width="20%" align="left" header-align="center"></el-table-column>
        <el-table-column prop="location" label="" min-width="17%" align="center"></el-table-column>
        <el-table-column prop="remark" label="" min-width="35%" align="left" :show-overflow-tooltip="true" header-align="center"></el-table-column>
        <!-------------------------------------------------------------->
        <el-table-column align="left" label="" min-width="8%" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" type="info" @click="_clickFlag('edit', '', '', scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--------------------------------------------------------------------->
    <div class="index-main-pagination" v-show="!listLoading">
      <div class="block" align="right">
        <div class="block">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="num"
            layout="total, prev, pager, next"
            :total="count.totalElements">
          </el-pagination>
        </div>
      </div>
    </div>
    <!-----------------------------------/---------------------->
    <el-dialog :title="dialogTitle" size="tiny" :visible.sync="dialogShow" align="left" @close="dialogInfo={}">
      <el-form ref="dialogInfo" :model="dialogInfo" :rules="rules">
        <el-form-item label="" :label-width="formLabelWidth" prop="code" >
          <el-input  auto-complete="off" v-model="dialogInfo.code" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="level">
          <el-radio-group v-model="dialogInfo.level">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4">LED</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="name" >
          <el-input  auto-complete="off" v-model="dialogInfo.name" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="location" >
          <el-input  auto-complete="off" v-model="dialogInfo.location" style="width: 80%" placeholder=""></el-input>
          <i class="el-icon-search" @click="_dialogMap(dialogInfo)"></i>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="remark" >
          <el-input  auto-complete="off" v-model="dialogInfo.remark" style="width: 80%" type="textarea" :rows="3" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false; dialogInfo={}"> </el-button>
        <el-button type="primary" @click="_add_submit(dialogInfo)"> </el-button>
      </div>
    </el-dialog>
    <!----------------------------------------------------------------------->
    <el-dialog :title="''+dialogInfo.location" size="large" :visible.sync="dialogMapShow" align="left" >
      <el-form ref=dialogMapInfo :model="dialogMapInfo">
        <div id="map"></div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogMapShow = false"> </el-button>
        <el-button type="primary" @click="_position"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
  import apiMethods from 'api/apiMethods.js'
  import {baseUrl} from 'api/config'
  import VMap from 'components/map'
  export default {
    components: {VMap},
    data() {
      let self = this;                   //
      return {
        path: '/inducePlate',            //
        listLoading: true,               //
        list: [],                        //
        delRow: [],
        page: 1,                         //
        num: 10,                         //
        searchInfo: {},                  //
        pagination: false,               //
        count: {                         //
          numberOfElements: 0,
          totalElements: 0,
        },
        dialogShow: false,               //
        dialogInfo: {
          location: ""
        },                  //
        dialogTitle: "",                 //
        dialogType: "",                  //
        formLabelWidth: '100px',
        dialogMapShow: false,
        dialogMapInfo: {},
        rules: {                           //
            name: [
              {required: true, type: 'string', message: "", trigger: 'blur'}
            ],
            code: [
              {required: true,  message: " ",  trigger: 'blur'}
            ],
            level: [
              {required: true,  message: " "}
            ]
        },
        coordinate: '',                    //
        position: ""

      }
    },
    created() {
      this._list(this.page)
    },
    methods: {
      _dialogMap(info) {
        this.dialogMapShow = true;
        this._initMap()
      },
      _position() {
        this.dialogMapShow = false
      },
      _initMap() {
        axios.post(baseUrl + '/config/findByCode/coordinate').then((res) => {
          if (res.data.code == 200) {
            var _this = this;
            this.coordinate = res.data.data[0].value
            // 
            var map = new AMap.Map('map', {
              resizeEnable: true,
              zoom: 15,
              scrollWheel: false
            });
            map.setCenter([this.coordinate.split(',')[0], this.coordinate.split(',')[1]])

            // 
            AMap.plugin([ 'AMap.ToolBar', 'AMap.Scale' ],
              function() {
                map.addControl(new AMap.ToolBar());
                map.addControl(new AMap.Scale());
              });
            //
             map.on('click', function(e) {
               _this.dialogInfo.location = e.lnglat.getLng() + ',' + e.lnglat.getLat()
            });
          }
        })
      }
    },
    mixins: [apiMethods]
  }
</script>

<style scoped>
  #map { width: 100%; height: 500px}
</style>




