<template>
  <div>
    <!-------------------------------------------------------------------------------->
    <div class="index-filter-container">
      <el-form :model="searchInfo" :inline="true" ref="searchInfo" label-position="left" label-width="0px" class="demo-ruleForm card-box loginform">
        <el-form-item prop="code">
          <el-input type="text" v-model="searchInfo.code"  placeholder="" @keyup.enter.native="_search(searchInfo)"></el-input>
        </el-form-item>
        <el-form-item prop="simCardId">
          <el-select v-model="searchInfo.simCardId"  placeholder="SIM" clearable>
            <el-option
              v-for="item in simIdList"
              :key="item.id"
              :label="item.phoneNumber"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="parkingId">
          <el-select v-model="searchInfo.parkingId"  placeholder="" clearable>
            <el-option
              v-for="item in parkingIdList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" type="primary"  icon="search"
                     @click="_search(searchInfo)"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button class="filter-item" style="margin-left: 10px;" @click="_clickFlag('add', '')" type="primary" icon="edit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!-------------------------------------------------------------->
    <div class="index-main-table">
      <el-table :data="list"  border v-loading="listLoading" element-loading-text="" fit highlight-current-row  style="width: 100%" >
        <el-table-column prop="code" label="" min-width="15%" align="center"></el-table-column>
        <el-table-column prop="phoneNumber" label="SIM" min-width="15%" align="center"></el-table-column>
        <el-table-column prop="parkingName" label="" min-width="15%" align="center"></el-table-column>
        <el-table-column prop="deviceStatus" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="remark" label="" min-width="15%" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="createTime" label="" min-width="20%" align="center"></el-table-column>
        <!-------------------------------------------------------------->
        <el-table-column align="center" label="" min-width="10%" header-align="center">
          <template slot-scope="scope">
            <el-button size="small" type="info" @click="_clickFlag('edit', '', '', scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--------------------------------------------------------------------->
    <div class="index-main-pagination" v-show="!listLoading">
      <div class="block" align="right">
        <div class="block">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="num"
            layout="total, prev, pager, next"
            :total="count.totalElements">
          </el-pagination>
        </div>
      </div>
    </div>
    <!-----------------------------------/---------------------->
    <el-dialog :title="dialogTitle" size="tiny" :visible.sync="dialogShow" align="left" @close="dialogInfo={}">
      <el-form ref="dialogInfo" :model="dialogInfo" :rules="rules">
        <el-form-item label="" :label-width="formLabelWidth" prop="code" >
          <el-input  auto-complete="off" v-model="dialogInfo.code" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="SIM" :label-width="formLabelWidth" prop="simCardId">
          <el-select v-model="dialogInfo.simCardId"  placeholder="" style="width: 80%">
            <el-option
              v-for="item in simIdList"
              :key="item.phoneNumber"
              :label="item.phoneNumber"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="parkingId">
          <el-select v-model="dialogInfo.parkingId"  placeholder="" style="width: 80%">
            <el-option
              v-for="item in parkingIdList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="deviceStatus">
          <el-radio-group v-model="dialogInfo.deviceStatus">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="remark" >
          <el-input  auto-complete="off" v-model="dialogInfo.remark" style="width: 80%" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogShow = false; dialogInfo={}"> </el-button>
        <el-button type="primary" @click="_add_submit(dialogInfo)"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/javascript">
  import apiMethods from 'api/apiMethods.js'
  import {baseUrl} from 'api/config'
  export default {
    data() {
      return {
        path: '/gateway',                //
        listLoading: true,               //
        list: [],                        //
        delRow: [],
        page: 1,                         //
        num: 10,                         //
        searchInfo: {},                  //
        pagination: false,               //
        count: {                         //
          numberOfElements: 0,
          totalElements: 0,
        },
        dialogShow: false,               //
        dialogInfo: {},                  //
        dialogTitle: "",                 //
        dialogType: "",                  //
        dialogDel: false,
        formLabelWidth: '100px',
        simIdList: [],
        parkingIdList: [],
        rules: {                           //
          code: [
            {required: true, type: 'string', message: "", trigger: 'blur'}
          ],
          simCardId: [
            {required: true,  message: "SIM"}
          ],
          parkingId: [
            {required: true,  message: ""}
          ],
          deviceStatus: [
            {required: true,  message: ""}
          ]
        }
      }
    },
    created() {
      this._list(this.page)
      this._getSimId()
      this._getParkingId()
    },
    methods: {
      _getSimId() {
        axios.post(baseUrl + '/simCard/list').then((res) => {
          if (res.data.code == 200) {
            this.simIdList = res.data.data
          }
        }).catch((err) => {
          console.log(err)
        })
      },
      _getParkingId() {
        axios.post(baseUrl + '/parking/list').then((res) => {
          if (res.data.code == 200) {
            this.parkingIdList = res.data.data
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    mixins: [apiMethods]
  }
</script>




